  
import React from 'react';
// import { findAllInRenderedTree } from 'react-dom/test-utils';
import './Signup.css';


  
const Signin = () => {
        const  messagesignin=()=>{
          document.getElementsByClassName("alert").innerHTML="Signed In successfully"
        }
        return (
                <div> 
                  <div className="message"></div>
                  <form onSubmit={ messagesignin}>
                   <div className="signin">
                       <div className="textsignin"><span >Enter Email Add.</span><input placeholder="abc@xyz.com" className="inputboxsignin" type="email"  required /></div>
                       <div className="textsignin"><span >Enter Password</span><input  placeholder="password.." className="inputboxsignin" type="password" pattern=".{8,}" title="Should contain 8 or more chars"  required /> </div> 
                       <div className="btnsignin"><input type="submit"  /></div>
                  </div> 
                  </form>
                  <div className="alert"></div>
               </div>
        );
        }
            


export default Signin;